.Car{
    margin-bottom: 10px;
    padding: 10px;
    box-shadow: 0 4px 5px 0 rgba(0,0,0,.14);
    border-radius: 5px;
    transition: border, box-shadow .3s;
}

.input {}

.input:active, .input:focus{
    outline: none;
}

.input.green{
    border: 1px solid green;
}
.input.red{
    border: 1px solid red;
}

.input.bold{
    border: 2px solid green;
    font-weight: bold;
}